import React, {Component} from 'react';

class Form extends Component {
	constructor(props) {
		super(props);
		this.state = {
			id:"",
			name: "",
			age: ""  
		}
	}

	handleFormData = (event) => {
		const target = event.target.name;
		const value = event.target.value;
		this.setState({[target]: value});
	}

	submitData = () => {
		this.props.addPeople(this.state);
	}

	render() {
		return (<form className="form">
					<div className="form-group">
	                  <label>Id</label>
	                  <input type="text" className="form-control" onChange={(event) => this.handleFormData(event)} name="id" value={this.state.id}/>
	                </div>

	                <div className="form-group">
	                  <label>Name</label>
	                  <input type="text" className="form-control" onChange={(event) => this.handleFormData(event)} name="name" value={this.state.name}/>
	                </div>
	                <div className="form-group">
	                  <label>Age</label>
	                  <input type="text" className="form-control" onChange={(event) => this.handleFormData(event)} name="age" value={this.state.age}/>
	                </div>
	                <div className="form-group">
	                  <button type="button" className="btn btn-primary btn-md" onClick={this.submitData}>Add Data</button>
	                </div>
              </form>)
	}

}

export default Form;