import React, {Component} from 'react';
import Table from './Components/Table';
import Form from './Components/Form';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      people : [],
      initalData : []
    }
  }

  addPeopleHandler = (data) => {
    const oldState = [...this.state.people,data];
    this.setState({people: oldState})
  }

  searchHandler = (event) => {
      const searchKey = event.target.value;
      const data = this.state.people.filter((item) => {
        return item.name.indexOf(searchKey) !== -1;
      });

    if(searchKey.length > 0) {
      this.setState({people: data});
    } else {
      this.setState({people: this.state.initalData});
    }
  }

  deleteHandler = (id) => {
      const data = this.state.people.filter((item) => {
        return item.id !== id;
      });
      this.setState({people: data});
  }

  render(){

    return (
        <div>
        <div className="container">
          <div className="row">
            <h1 className="text-center"> React Application</h1>
            <hr/>
            <br/>
            <div className="col-md-6">
            <h4> Add Data</h4>
            <hr/>
            <Form addPeople={this.addPeopleHandler}/>
            </div>
            <div className="col-md-6">
              <div className="row">
                  <div className="col-md-6">
                    <input type="text" placeholder="search here" className="form-control" onChange={(event) => this.searchHandler(event)}/>
                  </div>
              </div>
          <hr/>
        <Table people={this.state.people} deleteHandle={this.deleteHandler}/>
      </div>
    </div>
  </div>
</div>
      )
  }

}

export default App;


